body {
	#kimchi {
		width: 100%;
		.inner1 {
			width: 1180px;
			margin: 100px auto 20px;
			h1 {
				font: bold 50px/1 'Noto Serif KR';
				color: #666;
			}
			img {
				width: 250px;
				height: 250px;
				border-radius: 50%;
				object-fit: cover;
				border: 1px solid violet;
				position: relative;
				z-index: -1;
				opacity: 0.6;
				&[data-index='1'] {
					top: -70px;
				}
				&[data-index='2'] {
					top: 200px;
					right: -650px;
				}
			}
			.txt {
				p {
					width: 100%;
					font: bold 18px/1.7 'Noto Serif KR';
					color: #666;
					position: relative;
					top: -150px;
					padding: 10px;
				}
			}
			.bg1 {
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				height: 600px;
				object-fit: cover;
				opacity: 0.1;
			}
		}
		.banner {
			width: 100%;
			height: 300px;
			background-image: url(../../../public/img/chillies.jpg);
			background-repeat: no-repeat;
			background-position: center center;
			background-size: cover;
			background-attachment: fixed;
			display: flex;
			justify-content: space-between;
			align-items: center;
			h2 {
				position: absolute;
				top: 690px;
				left: -452px;
				font: bold 100px/1 'arial';
				color: red;
			}
			.inner3 {
				width: 1180px;
				height: 100%;
				background-color: rgba(0, 0, 0, 0.2);
				margin: 0 auto;
				p {
					margin-top: 15px;
					padding: 10px;
					font: normal 17px/1.3 'Noto Serif KR';
					color: #fff;
				}
			}
		}
	}
	#map {
		width: 100%;
		height: 5800px;
		background-color: #000;
		overflow: hidden;
		.bg2 {
			width: 100%;
			height: 600px;
			object-fit: cover;
			opacity: 0.1;
			filter: brightness(0.6);
		}
		.bg3 {
			position: relative;
			top: 4500px;
			width: 100%;
			height: 750px;
			object-fit: cover;
			opacity: 0.1;
			filter: brightness(0.6);
		}
		div {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			top: 50%;
			width: 80%;
			border-radius: 10px;
			h2 {
				position: absolute;
				bottom: 400px;
				left: 150px;
				font: bold 32px/1 'Noto Serif KR';
				color: #777;
				margin-bottom: 10px;
			}
			h3 {
				position: absolute;
				font: normal 36px/1 'Nanum Myeongjo';
				color: #777;
				margin-bottom: 10px;
				right: 50px;
			}
			p {
				font: bold 1vw/1.2 'Noto Serif KR';
				color: #777;
				padding: 20px;
			}
			img {
				width: 300px;
				object-fit: cover;
				border-radius: 20px;
			}
			.pic {
				width: 300px;
				border-radius: 10px;
				filter: brightness(1.3);
			}
		}
		.left {
			position: absolute;
			left: -800px;
			top: 2400px;
			h2 {
				position: absolute;
				bottom: 450px;
			}
			h3 {
				bottom: 450px;
			}
		}
		.right {
			position: absolute;
			right: -2000px;
			top: 3050px;
			h2 {
				bottom: 500px;
				left: 50px;
			}
			h3 {
				bottom: 500px;
			}
		}
		.left1 {
			position: absolute;
			left: -800px;
			top: 3700px;
			h2 {
				left: 300px;
				bottom: 400px;
			}
			h3 {
				bottom: 400px;
			}
		}
		.right1 {
			position: absolute;
			right: -1900px;
			top: 4600px;
			h2 {
				left: 50px;
				bottom: 500px;
			}
			h3 {
				bottom: 500px;
			}
			.pic {
				width: 300px;
			}
		}
		.left2 {
			position: absolute;
			left: -800px;
			top: 5500px;
			h2 {
				left: 350px;
				bottom: 500px;
			}
			h3 {
				bottom: 500px;
			}
			.pic {
				width: 300px;
			}
		}
	}
	#process {
		position: relative;
		width: 100%;
		height: 500px;
		overflow: hidden;
		margin: 150px 0;
		.panel2 {
			position: absolute;
			top: 0;
			left: 0;
			width: calc(100% * 3);
			height: 100%;
			li {
				width: calc(100% / 3);
				height: 100%;
				float: left;
				padding: 0 100px;
				.text {
					width: 800px;
					height: 100px;
					position: relative;
					font: bold 48px/1 'Noto Serif KR';
					color: #fff;
					top: 50px;
					left: 100px;
					background-color: rgba(0, 0, 0, 0.3);
					padding: 20px;
					transition: 0.2s;
					transform: translateY(0px);
					opacity: 1;
					&.off {
						transform: translateY(150px);
						opacity: 0;
					}
				}
				.text1 {
					width: 50%;
					position: relative;
					top: 75px;
					left: 40%;
					font: normal 18px/1.4 'Noto Serif KR';
					color: #fff;
					background-color: rgba(0, 0, 0, 0.3);
					padding: 20px;
					transition: 0.5s;
					transform: translateY(0px);
					opacity: 1;
					&.off {
						transform: translateY(150px);
						opacity: 0;
					}
				}
				&[data-index='1'] {
					background: url(../../../public/img/배추절이는사진.jpg) no-repeat
						center/cover;
				}
				&[data-index='2'] {
					background: url(../../../public/img/김장.jpg) no-repeat center/cover;
				}
				&[data-index='3'] {
					background: url(../../../public/img/김치보관.png) no-repeat
						center/cover;
				}
			}
		}
		.prev {
			position: absolute;
			top: 200px;
			left: 20px;
			opacity: 0.9;
		}
		.next {
			position: absolute;
			top: 200px;
			right: 20px;
			opacity: 0.9;
		}
	}
}
