figure {
	width: 100%;
	height: calc(100vh - 60px);
	background-color: #333;
	video {
		width: 100%;
		height: 100%;
		object-fit: cover;
		opacity: 0.6;
	}
	.inner {
		width: 1180px;
		margin: 0 auto;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		h2 {
			font: normal 120px/1 'arial';
			color: #fff;
			margin-bottom: 5px;
		}
		span {
			display: block;
			font: normal 36px/1 'Noto Serif KR';
			color: #fff;
			margin-bottom: 80px;
		}
		p {
			font: 18px/1.4 'Noto Serif KR';
			color: #fff;
			margin-bottom: 100px;
		}
		a {
			display: block;
			width: 400px;
			height: 35px;
			border: 1px solid #bbb;
			text-align: center;
			font: bold 14px/35px 'Noto Serif KR';
			color: #fff;
			&:hover {
				background-color: #fff;
				color: #444;
			}
		}
	}
}
@media screen and (max-width: 1210px) {
	figure {
		width: 100%;
		height: 80vh;
		background: url(../../../public/img/김장리액트.jpg) no-repeat center/cover;
		video {
			display: none;
		}
		.inner {
			width: 100%;
			padding: 0 20px;
			background-color: rgba(0, 0, 0, 0.5);
			h2 {
				color: burlywood;
				font: normal 80px/1 'arial';
			}
			span {
				color: burlywood;
				margin-bottom: 10px;
			}
			p {
				color: burlywood;
				margin-bottom: 25px;
			}
			a {
				width: 50%;
				color: burlywood;
				border: 1px solid burlywood;
				margin-bottom: 20px;
				&:hover {
					background-color: brown;
					color: burlywood;
				}
			}
		}
	}
}
@media screen and (max-width: 280px) {
	figure {
		.inner {
			h2 {
				margin-top: 10px;
				margin-bottom: 10px;
				font: normal 20px/1 'arial';
			}
			span {
				margin-bottom: 10px;
				font: normal 28px/1 'Noto Serif KR';
			}
			p {
				font: 12px/1.6 'Noto Serif KR';
				margin-bottom: 50px;
			}
			a {
				height: 30px;
				font: bold 8px/30px 'Noto Serif KR';
				width: 80px;
			}
		}
	}
}
@media screen and (max-height: 924px) {
	figure {
		.inner {
			top: 55%;
			h2 {
				color: burlywood;
				font: normal 24px/1 'arial';
				margin-bottom: 0px;
			}
			span {
				color: burlywood;
				margin-bottom: 5px;
				font: normal 18px/1 'Noto Serif KR';
			}
			p {
				font: 10px/1.5 'Noto Serif KR';
				color: burlywood;
				margin-bottom: 5px;
			}
			a {
				height: 30px;
				font: bold 6px/30px 'Noto Serif KR';
				width: 100px;
				color: burlywood;
				margin-bottom: 10px;
			}
		}
	}
}
